/* eslint-disable react/prop-types */
import React from 'react';

import { List, ListItem, makeStyles } from '@material-ui/core';
import { ChevronRight, ExpandMore } from '@material-ui/icons';
import { useSelector } from 'react-redux';
import TreeMenu, {
  TreeNodeInArray,
  TreeMenuItem,
} from 'react-simple-tree-menu';
import { useSubCategory } from 'src/lib/hooks';

import { AppState } from './../../../state';
const useStyles = makeStyles((theme) => ({
  list: {},
  listItem: {
    cursor: 'pointer',
    position: 'relative',
  },
}));

type CategoryViewerProps = {
  className?: string;
};

const DEFAULT_PADDING = 16;
const ICON_SIZE = 8;
const LEVEL_SPACE = 16;

// TODO: customize
const TreeListItem: React.FC<TreeMenuItem> = ({
  level = 0,
  hasNodes,
  isOpen,
  label,
  toggleNode,
  focused,
}: TreeMenuItem) => {
  const classes = useStyles();
  return (
    <ListItem
      className={`${classes.listItem} level-${level}`}
      style={{
        paddingLeft: DEFAULT_PADDING + ICON_SIZE + level * LEVEL_SPACE,
        // boxShadow: focused ? '0px 0px 5px 0px #222' : 'none',
        // zIndex: focused ? 999 : 'unset',
      }}>
      {hasNodes && (
        <div
          style={{ display: 'inline-block' }}
          onClick={(e) => {
            hasNodes && toggleNode && toggleNode();
            e.stopPropagation();
          }}>
          {isOpen ? <ExpandMore /> : <ChevronRight />}
        </div>
      )}
      {label}
    </ListItem>
  );
};

export const CategoryViewer: React.FC<CategoryViewerProps> = (
  props: CategoryViewerProps
) => {
  const classes = useStyles();
  const sections = useSelector((st: AppState) => st.kuralIndexMap.section);
  const [, getLiteraturesBySection, getChaptersByLiterature] = useSubCategory();

  const TREE_DATA: TreeNodeInArray[] = Object.values(sections).map(
    (s, sIndex) => ({
      key: s.id.toString(),
      label: s.value,
      index: sIndex,
      nodes: getLiteraturesBySection(s.id).map((lit, litIndex) => ({
        key: lit.id.toString(),
        label: lit.value,
        index: litIndex,
        nodes: getChaptersByLiterature(lit.id).map((ch, chIndex) => ({
          key: ch.id.toString(),
          label: ch.value,
          index: chIndex,
        })),
      })),
    })
  );

  return (
    <TreeMenu data={TREE_DATA} hasSearch={false}>
      {({ items }) => (
        <List className={classes.list}>
          {items.map(({ key, ...itemProps }) => (
            <TreeListItem key={key} {...itemProps} />
          ))}
        </List>
      )}
    </TreeMenu>
  );
};
